export const JobsGraphqlSchema = `
  enum CurrentStatus {
    active
    hold
    expired
    closed
    urgent
  }
  type Resume {
    path: String!
    user_id: String!
  }
  type Job {
    id: String!
    name: String!
    type: String!
    category: [String!]!
    desc: String!
    skills_required: [String!]!
    sallary_min: Float!
    sallary_max: Float!
    location: String
    attachment: String
    resume: Resume
    current_status: CurrentStatus!
    views: Int
    applyJob: [ApplyJob!]
  }
  extend type Query {
    createJob: Job
    fetchJob: Job
  }
`
